import React from 'react';
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import "./CardView.css"
import "bootstrap/dist/css/bootstrap.min.css";

function CardView(props){
    return(
        <Col className="CardView" style={{"margin":"100px 40px 130px 40px"}} lg={3}>
        <Card>
          <Card.Img variant="top" height="250" width="250" src={props.img} />
          <Card.Body>
            <Card.Title>{props.title}</Card.Title>
            <Card.Text>
                {props.desc}
            </Card.Text>
            <a href={props.href} target="_demo"><Button variant="primary">Watch Now</Button></a>
          </Card.Body>
        </Card>
      </Col>
    )
}

export default CardView;