import React from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import "./App.css";
import Card from "./Componets/CardView"
import movieData from "./Data/movie"
import NavBar from './Componets/NavBar'
function nCard(val){
    return(
        <Card title={val.name} desc={val.desc} href={val.href} img={val.img}/>
    )
}

function App() {
  return (
    <React.Fragment>
      <NavBar></NavBar>
      
      <Row style={{"justifyContent":"center"}}>
        {movieData.map(nCard)}
      </Row>
    
    </React.Fragment>
  );
}

export default App;
